"use client";
import { useEffect, useState } from "react";
import { onAuthStateChanged } from "firebase/auth";
import { doc, getDoc } from "firebase/firestore";
import { useRouter } from "next/navigation";
import { auth, db } from "@/firebase/firebase";
import Sidebar from "@/components/sidebar";

export default function AdminLayout({ children }) {
  const router = useRouter();
  const [authorized, setAuthorized] = useState(null); // null: loading, true: ok, false: bị chặn

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (user) => {
      if (!user) {
        router.push("/dang-nhap");
        return;
      }

      const userDoc = await getDoc(doc(db, "users", user.uid));
      const userData = userDoc.data();

      if (userData?.role === "admin") {
        setAuthorized(true);
      } else {
        setAuthorized(false);
      }
    });

    return () => unsubscribe();
  }, [router]);

  // Đang kiểm tra quyền
  if (authorized === null) {
    return (
      <div className="w-full h-screen flex items-center justify-center bg-gray-100 text-black">
        <p>Đang xác minh quyền truy cập...</p>
      </div>
    );
  }

  // Không phải admin
  if (authorized === false) {
    return (
      <div className="w-full h-screen flex items-center justify-center bg-red-100 text-red-600 text-xl font-semibold">
        <p>Bạn không có quyền truy cập vào khu vực quản trị.</p>
      </div>
    );
  }

  // Là admin → render giao diện admin đầy đủ
  return (
    <div className="flex h-screen">
      <Sidebar />
      <main className="ml-64 w-full h-screen overflow-y-auto bg-gray-100 p-6">
        {children}
      </main>
    </div>
  );
}
